import React, { Component } from 'react'
import { connect } from 'react-redux'
import * as todoActions from '../actions'
import TodoForm from './TodoForm'
import TodoList from './TodoList'
import TodoDisplay from './TodoDisplay'

class Todo extends Component {
    constructor() {
        super()
        this.handleSubmit = this.handleSubmit.bind(this);
        this.state = { title: '', date:'', description: '', id: '', mode: 0}
    }

    handleSubmit(e) {
            e.preventDefault();
            if (this.state.title && this.state.date && this.state.description) {
                this.props.createTodoItem({ 
                title: this.state.title, date: this.state.date, description: this.state.description
            }, this.state.id);
            this.setState({ title: '', date:'', description: '', id: '',mode: 0})
        }
    }

    handleEdit(item,index){
        this.setState({title: item.title, id: index, 
            description: item.description,date:item.date,mode: 1
        })
    }

    handleView(item,index){
        this.setState({title: item.title, id: index, 
            description: item.description,date:item.date, mode: 2
        })
    }

    renderSelected(mode){
        switch(mode){
            case 0: return <TodoList data={this}/>;
            case 1: return <TodoForm update={this}/>;
            case 2: return <TodoDisplay data={this}/>;
            default:
                return <TodoList data={this}/>
        }

    }
 
    render() {
        return (
            <div>
                {this.renderSelected(this.state.mode)}
            </div>
        )
    }
}

const stateToProps = (state, ownProps) => {
    return { todo: state.todo }
}

const dispatchToProps = (dispatch) => {
    return {
        createTodoItem: (todo, id) => dispatch(todoActions.createTodoItem(todo, id)),
        deleteTodo: index => dispatch(todoActions.deleteTodo(index))
    }
}

export default connect(stateToProps, dispatchToProps)(Todo);


