import React, { Component } from 'react'

class TodoList extends Component {
    render() {
        return (
        <div>
            <table className="table"> 
                <tbody>
                    {this.props.data.props.todo.map((item, index) => {   
                        return <tr key={index}>
                        <td>{item.title}</td>
                        <td><button className="btn-primary" onClick={() => this.props.data.props.deleteTodo(index)} >X</button></td>
                        <td><button className="btn-primary" onClick={() => this.props.data.handleEdit(item,index)}>edit</button></td>
                        <td><button className="btn-primary" onClick={() => this.props.data.handleView(item,index)}>view</button></td></tr>})}
                </tbody>
            </table>
            <button className="btn-primary" onClick={()=>this.props.data.setState({mode:1})}>AddTodo</button>
        </div>
        )
    }
}

export default TodoList


