import constants from '../constants'

export const createTodoItem = (todo,index) => {
    return {
        type: constants.CREATE_TODO_ITEM,
        data: todo,
        id: index
    }
  };

export const deleteTodo = (id) => {
    return {
        type: constants.DELETE_TODO_ITEM,
        id: id
    }
}



