import React, { Component } from 'react';
import Todo from './components/Todos'
import {Provider} from 'react-redux';
import store from './store'

class App extends Component {
  render() {
    return (
      <Provider store={store.createStore()}>
       <Todo/>
      </Provider>
    );
  }
}

export default App;



