import React, { Component } from 'react'

class TodoDisplay extends Component {

    render() {
        return (
            <div>
                <div>
                    <h4>Title: {this.props.data.state.title}</h4><br />
                    <h4>Description: {this.props.data.state.description}</h4><br />
                    <h4>Date: {this.props.data.state.date}</h4>
                </div>
                <button className="btn-primary" onClick={(e) => this.props.data.setState({ mode: 0 })}>Back to list</button>
            </div>
        )
    }
}

export default TodoDisplay


