import React, { Component } from 'react'

class TodoForm extends Component {
    render() {
        return (
            <form onSubmit={this.props.update.handleSubmit} className="form-stacked">
                <div className="form-group">
                    <label htmlFor="title">Title:</label>
                    <input onChange={(e) => { this.props.update.setState({ title: e.target.value, view: false }) }}
                        value={this.props.update.state.view ? '' : this.props.update.state.title} type="title" className="form-control" id="title" />
                </div>
                <div className="form-group">
                    <label htmlFor="desc">Description:</label>
                    <input onChange={(e) => {this.props.update.setState({ description: e.target.value, view: false }) }}
                        value={this.props.update.state.view ? '' : this.props.update.state.description} type="desc" className="form-control" id="desc" />
                </div>
                <div className="form-group">
                    <label htmlFor="date">Date:</label>
                    <input onChange={(e) => { this.props.update.setState({ date: e.target.value, view: false }) }}
                        value={this.props.update.state.view ? '' : this.props.update.state.date} type="date" className="form-control" id="date" />
                </div>
                <input type='submit' className="btn-secondary" />
            </form>
        )
    }
}

export default TodoForm


