import constants from '../constants'

export default (state = [], action) => {

    switch (action.type) {
        case (constants.CREATE_TODO_ITEM):
            if (action.id !== '') {
                return [...state.filter((data, i) => i !== action.id),Object.assign({}, action.data)]
                 } else {
                const exist = state.filter((data, i) => data.title === action.data.title)
                return exist.length > 0 ? state : [...state, Object.assign({}, action.data)]
            }
        case (constants.DELETE_TODO_ITEM):
            return state.filter((data, i) => i !== action.id);

        default:
            return state
    }

}


